}
.hamburger__line1 {
  stroke-dasharray: 60 207;
  stroke-width: 3;
}
.hamburger__line2 {
  stroke-dasharray: 60 60;
  stroke-width: 3;
}
.hamburger__line3 {
  stroke-dasharray: 60 207;
  stroke-width: 3;
}
.opened .hamburger__line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 3;
}
.opened .hamburger__line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 3;
}
.opened .hamburger__line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 3;
}

