:root {
  --font-neonblitz: "Neonblitz";
  --font-racespace-reg: "Racespace Reglr";
  --font-racespace-strp: "Racespace Strp";
  --font-terabite: "Terabite";
  --font-tetillium: "Titillium";
  --font-tetillium-lit: "Titillium Lit";
  --font-tetillium-bld: "Titillium Bld";
  --primary: #528ce4;
  --light: #f8f9fa;
  --grey: #6c757d;
  --orange: #ffc107;
  --danger: #dc3545;
  --dark: #212529;
  --primary-background: linear-gradient(225deg, #ffd89b 10%, #19547b 70%);
  --secondary-background: linear-gradient(45deg, #ffd89b 10%, #19547b 70%);
  --focus-background: linear-gradient(45deg, #ffd89b 10%, #dc3545 70%);
  --btn-hover-boxshadow: rgba(0, 0, 0, 0.099) 6px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) 6px 8px 8px 0px, rgba(0, 0, 0, 0.099) -6px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) -6px 8px 8px 0px;
  --btn-focus-boxshadow: rgba(0, 0, 0, 0.099) 0px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) 0px 8px 8px 0px;
  --card-boxshadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
  --box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

*::-webkit-scrollbar,
*::-webkit-scrollbar-thumb,
*::-webkit-scrollbar-track {
  display: none !important;
  opacity: 0 !important;
}

*::selection {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
}

* {
  border: none;
  outline: none;
  cursor: none !important;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.2;
  overflow-x: hidden;
}

a,
a:active a:visited {
  text-decoration: none;
}

.cursor {
  position: fixed;
  top: -2.5em;
  left: -2.5em;
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
}
.cursor::before {
  content: "";
  width: 5em;
  height: 5em;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: var(--light);
  border: 0.0625em solid var(--light);
  transition: 300ms;
  transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
  transform: scale(0.15);
}

.cursor.button {
  mix-blend-mode: unset;
}
.cursor.button::before {
  border: none;
  transform: scale(0.95);
  filter: opacity(0.55);
  background: linear-gradient(120deg, var(--danger), var(--orange));
}

.cursor.drag {
  mix-blend-mode: unset;
}
.cursor.drag::before {
  transform: scale(1);
  background: var(--primary);
  border-color: transparent;
  content: "î¨£";
  font-family: "LineIcons";
  font-size: 1.25em;
  color: #fff;
}

.cursor.navlinks {
  mix-blend-mode: unset;
}
.cursor.navlinks::before {
  content: "";
  transform: scale(0.75);
  filter: opacity(0.25);
  border: none;
  background: linear-gradient(120deg, var(--primary), var(--danger));
}

.cursor.brand {
  mix-blend-mode: unset;
}
.cursor.brand::before {
  content: "";
  transform: scale(0.95);
  filter: opacity(0.55);
  border: none;
  background: linear-gradient(120deg, var(--light), var(--primary));
}

.cursor.show__hamburger {
  mix-blend-mode: unset;
}
.cursor.show__hamburger::before {
  content: "";
  transform: scale(0.75);
  filter: opacity(0.25);
  border: none;
  background: linear-gradient(360deg, cyan, magenta);
}

.preloader {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background: var(--dark);
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
  transition-delay: 1s;
  transition-delay: 1s;
}
.preloader .loadbar {
  width: 0%;
  height: 100%;
  max-height: 0.375em;
  background: var(--primary);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  overflow: hidden;
}
.preloader figure {
  width: 37.5em;
  height: 37.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fade 0.3s;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
  position: relative;
}
.preloader figure span {
  width: 100%;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  color: var(--light);
  text-align: center;
  letter-spacing: 0.1875em;
  font-family: var(--font-galaxus);
  font-size: 1em;
}
.preloader figure img {
  height: 6.25em;
  display: inline-block;
  opacity: 0.05;
}

@keyframes fade {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.page-loaded .preloader {
  height: 0;
}
.page-loaded .preloader .loadbar {
  max-height: 100%;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
}
.page-loaded .preloader figure {
  transform: translateY(50px);
  opacity: 0;
}

.custom__button {
  position: relative;
  display: block;
  width: 8.75em;
  height: 3.4375em;
  font-family: var(--font-titillium-bold);
  font-size: 1em;
  letter-spacing: 0.0625em;
  color: white;
  background: var(--primary);
  overflow: hidden;
  z-index: 1;
}
.custom__button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, orange, magenta);
  transform-origin: right;
  transform: scaleX(0);
  transition: transform 400ms;
  transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
  z-index: -1;
}
.custom__button:hover::after {
  transform-origin: left;
  transform: scaleX(1);
}

.link__button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 8.75em;
  height: 3.4375em;
  font-family: var(--font-titillium-bold);
  font-size: 1em;
  letter-spacing: 0.0625em;
  color: white !important;
  background: var(--primary);
  overflow: hidden;
  z-index: 1;
}
.link__button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, orange, magenta);
  transform-origin: right;
  transform: scaleX(0);
  transition: transform 400ms;
  transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
  z-index: -1;
}
.link__button:hover::after {
  transform-origin: left;
  transform: scaleX(1);
}

